import {apiService} from "../services/api.service";

/**
 *  –û—Ç –≤—ã–±–æ—Ä–∞ –≤ A(–ø–µ—Ä–≤–æ–º —Å–µ–ª–µ–∫—Ç), –∑–∞–≤–∏—Å–∏—Ç –Ω–∞–ø–æ–ª–Ω–µ–Ω–∏—è –ë(–≤—Ç–æ—Ä–æ–≥–æ —Å–µ–ª–µ–∫—Ç–µ)
 * */
export default class ChainingSelectInFormPlugin {

  /**
   * –ö–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä
   * @param {string | Element} form  - —Ñ–æ—Ä–º–∞ –≤ –∫–æ—Ä–æ—Ä—ã–π –Ω–∞–¥–æ –æ–±—ä–µ–¥–∏–Ω–∏—Ç—å –¥–≤–∞ –≤—ã–ø–æ–¥–∞—é—â–∏–π —Å–ø–∏—Å–∫–∞.
   * @param {Object} selectA         - –≤—ã–ø–æ–¥–∞—é—â–∏–π —Å–ø–æ—Å–æ–∫ A
   * @param {string} [selectA.selectorA] - —Å–µ–ª–µ–∫—Ç–æ—Ä –≤—ã–ø–æ–¥–∞—é—â–µ–≥–æ —Å–ø–∏—Å–∫–∞ –ê
   * @param {string} [selectA.selectATriggerSelector] - —Å–µ—Ç–µ–∫—Ç–æ—Ä –¥–ª—è –∑–∞–ø—É—Å–∫–∞ –¥–µ–π—Å—Ç–≤–∏—è –≤ –≤—ã–ø–æ–¥–∞—é—â–∏–º —Å–ø–∏—Å–∫–µ –ê
   * @param {string} [selectA.callAction] - –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–æ—Ç–æ—Ä—ã–π –≤—ã–∑—ã–≤–∞–µ—Ç –≤—ã–ø–æ–¥–∞—é—â–∏–π —Å–ø–æ—Å–æ–∫ A –¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏—è –¥–∞–Ω–Ω—ã—Ö, –∫–æ—Ç–æ—Ä—ã–µ –ø–æ—Ç–æ–º –æ—Ç—Ä–∏—Å—É–µ–º –≤ –≤—ã–ø–æ–¥–∞—é—â–∏–π —Å–ø–∏—Å–∫–∏ –í
   * @param {Object} selectB         - –≤—ã–ø–æ–¥–∞—é—â–∏–π —Å–ø–æ—Å–æ–∫ –í
   * @param {string} [selectB.selectorB]  -  —Å–µ–ª–µ–∫—Ç–æ—Ä –≤—ã–ø–æ–¥–∞—é—â–µ–≥–æ —Å–ø–∏—Å–∫–∞ –í
   * @param {Object} [selectB.resetParams]  - —ç–ª–µ–º–µ–Ω—Ç–∞ –¥–ª—è —Å–±—Ä–æ—Å–∞
   * @param {string} [selectB.resetParams.activeOption]  - –∞–∫—Ç–∏–≤–Ω—ã–π –ø—É–Ω–∫—Ç
   * @param {string} [selectB.resetParams.options]  - –ø—É–Ω–∫—Ç—ã —Å–ø–∏—Å–∫–∞
   * @param {string} [selectB.resetParams.input]  - input –¥–ª—è –æ—Ç–ø—Ä–∞–≤–∫–∏ –Ω–∞ —Å–µ—Ä–≤–µ—Ä
   * @param {Function} [selectB.renderTemplate]  - —à–∞–±–ª–æ–Ω –∫–æ—Ç–æ—Ä—ã–π –ø—Ä–∏–Ω–µ–º–∞–µ—Ç –¥–∞–Ω–Ω—ã–µ –∏–∑ —Å–ø–∏—Å–∫–∞ A, —Å —Å–æ–∑–¥–∞–µ—Ç html –≤–µ—Ä—Å—Ç–∫—É –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è —Å–ø–∏—Å–∫–∞ B
   * @param {string} [selectB.selectorWherePasteOptions]  - —à–∞–±–ª–æ–Ω –∫–æ—Ç–æ—Ä—ã–π –ø—Ä–∏–Ω–µ–º–∞–µ—Ç –¥–∞–Ω–Ω—ã–µ –∏–∑ —Å–ø–∏—Å–∫–∞ A, —Å —Å–æ–∑–¥–∞–µ—Ç html –≤–µ—Ä—Å—Ç–∫—É –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è —Å–ø–∏—Å–∫–∞ B
   */
  constructor(form,
              {
                selectorA = '[data-sumbiot-selectA]',
                selectATriggerSelector = '.dropdown__item',
                callAction
              } = {},
              {
                selectorB = '[data-sumbiot-selectB]',
                resetParams= {
                  activeOption : '.js-dropdown__toggle',
                  options: '.dropdown__item',
                  input: '.sumbiot-input-select'
                },
                renderTemplate,
                selectorWherePasteOptions= '[data-sumbiot-selectB-paste]'
              } = {}) {

    this.$form = form.tagName ? form : document.querySelector(form)

    this.$selectA = this.$form.querySelector(selectorA)
    this.selectATriggerSelector = selectATriggerSelector
    this.callActionA = callAction

    this.$selectB = this.$form.querySelector(selectorB)
    this.$pasteOptions = this.$selectB.querySelector(selectorWherePasteOptions)
    this.resetParamsB = resetParams
    this.renderTemplateB = renderTemplate

    this._init()
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –ø–ª–∞–≥–∏–Ω–∞
   * @return {void}
   */
  _init() {

    this._handlerEvent()

  }

  /**
   * –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏–π
   * @return {void}
   */
  _handlerEvent() {

    this.$selectA.addEventListener('click', (e) => {
      let target = e.target;

      if (target && target.classList.contains(this.selectATriggerSelector.slice(1)) || target && target.parentElement.classList.contains(this.selectATriggerSelector.slice(1)) ) {
        e.preventDefault()

        if (target.parentElement.classList.contains(this.selectATriggerSelector.slice(1))){
          target = target.parentElement
        }

        this.fillSelectB(target)
      }
    })

  }

  /**
   * –ó–∞–ø–æ–ª–Ω–∏—Ç—å –≤—ã–ø–æ–¥–∞—é—â–∏–π —Å–ø–∏—Å–æ–∫ B
   * @param {HTMLElement} option - –ø—É–Ω–∫—Ç —Å–ø–∏—Å–∫–∞ –≤ –ê —Å–µ–ª–µ–∫—Ç–µ
   * @return {void}
   */
  async fillSelectB(option) {

    try {

      let idSelectA = option.dataset.selectOption,
          formData = new FormData()

      formData.append('id',idSelectA)

      this._resetSelectB()

      const response = await apiService.useRequest(this.callActionA,formData),
            result = JSON.parse(response.data.result)

      if(Array.isArray(result) && result.length) {

        let html = result.map(option => {
          return this.renderTemplateB(option)
        })

        this.$pasteOptions.insertAdjacentHTML('afterbegin', html.join(''))

      } else {

        console.error('In file ChainingSelectInFormPlugin, in function fillSelectB, response is either not an array or an empty array')

      }

    } catch (error) {

      if(error.status === 'error') {

        console.group(`In file ApiService, in function , ${this.callActionA} promise return reject`)

          console.group('List of errors')

          error.errors.forEach(error => {
            console.error(`Name: ${error.message}\n Code: ${error.code}\n customData: ${error.customData}`)
          })

          console.groupEnd();

        console.groupEnd();

      } else {

        console.group('In file ChainingSelectInFormPlugin, in function fillSelectB error')
          console.error(`${error.stack}`)
        console.groupEnd();

      }
    }

  }

  /**
   * –°–±—Ä–æ—Å –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤ —É –≤—ã–ø–æ–¥–∞—é—â–∏–π —Å–ø–∏—Å–æ–∫–∞ B
   * @return {void}
   */
  _resetSelectB() {
    let toggle = this.$selectB.querySelector(this.resetParamsB.activeOption),
        options = this.$selectB.querySelectorAll(this.resetParamsB.options),
        input = this.$selectB.parentElement.querySelector(this.resetParamsB.input)

    // –∞–∫—Ç–∏–≤–Ω—ã–π –ø—É–Ω–∫—Ç
    toggle.innerText = ''
    toggle.removeAttribute('title')

    // –ø—É–Ω–∫—Ç—ã —Å–ø–∏—Å–∫–∞
    options.forEach(option => {
      option.remove()
    })

    // input
    input.setAttribute('value','')
  }

  /**
   * –£–¥–∞–ª–µ–Ω–∏–µ –ø—É–Ω–∫—Ç–æ–≤ –≤—ã–ø–æ–¥–∞—é—â–∏–π —Å–ø–∏—Å–æ–∫–∞ B
   * @return {void}
   */
  deleteOptions() {
    let options = this.$selectB.querySelectorAll(this.resetParamsB.options)

    // –ø—É–Ω–∫—Ç—ã —Å–ø–∏—Å–∫–∞
    options.forEach(option => {
      option.remove()
    })
  }

}
